<template>
  <div>
    <div style="display: flex;justify-content: center;margin-top: 100px">
      <el-card style="width: 400px">
        <div slot="header" class="clearfix">
          <span>注册</span>
        </div>
        <!--<table>
          <tr>
            <td>电话</td>
            <td>
              <el-input type="tel" v-model="user.phonenumber" placeholder="请输入手机号"></el-input>
            </td>
          </tr>
          <tr>
            <td>用户名</td>
            <td>
              <el-input v-model="user.username" placeholder="请输入用户名"></el-input>
            </td>
          </tr>
          <tr>
            <td>邮箱</td>
            <td>
              <el-input v-model="user.email" placeholder="请输入邮箱"></el-input>
            </td>
          </tr>
          <tr>
            <td>密码</td>
            <td>
              <el-input type="password" v-model="user.password1" placeholder="请输入密码" ></el-input>
            </td>
          </tr>
          <tr>
            <td>请确认密码</td>
            <td>
              <el-input type="password" v-model="user.password2" placeholder="请确认密码" @keydown.enter.native="doLogin"></el-input>
            </td>
          </tr>
          <tr>
            <td colspan="2">
              <el-button style="width: 350px" type="primary" @click="doRegister">注册</el-button>
            </td>
          </tr>
        </table>-->
        <el-form ref="infoForm" :model="user" :rules="rules"  :validate-on-rule-change="true">
          <el-form-item label="手机号" prop="phonenumber" required="true">
            <el-input
                type="tel"
                placeholder="请输入手机号"
                v-model="user.phonenumber"
                show-word-limit
                style="width: 75%; float: right"
              >
            </el-input>
          </el-form-item>

          <el-form-item label="用户名" prop="username" required="true">
            <el-input
                placeholder="请输入用户名"
                v-model="user.username"
                maxlength="15"
                show-word-limit
                style="width: 75%; float: right"
              >
            </el-input>
          </el-form-item>

          <el-form-item label="邮箱" prop="email" required="true">
            <el-input
                type="email"
                placeholder="请输入邮箱"
                v-model="user.email"
                show-word-limit
                style="width: 75%; float: right"
              >
            </el-input>
          </el-form-item>

          <el-form-item label="密码" prop="password1" required="true">
            <el-input
                type="password"
                placeholder="请输入密码"
                v-model="user.password1"
                maxlength="20"
                show-word-limit
                style="width: 75%; float: right"
              >
            </el-input>
          </el-form-item>

          <el-form-item label="确认密码" prop="password2" required="true">
            <el-input
                type="password"
                placeholder="请再次输入密码"
                v-model="user.password2"
                maxlength="20"
                show-word-limit
                style="width: 75%; float: right"
              >
            </el-input>
          </el-form-item>

          <el-form-item prop="Intro">
            <el-row>
              修改个性签名：
              <el-input
                type="textarea"
                style="width: 400px;"
                placeholder="请输入15字以内个性签名"
                :autosize="{ minRows: 2, maxRows: 4}"
                v-model="user.Intro">
              </el-input>
            </el-row>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" style="width: 100%"  @click="doRegister">注册</el-button>
          </el-form-item>
        </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>
import {postNewUser} from '../../api/api.js'
export default {
  data () {
    return {
      user: {
        phonenumber: '',
        username: '',
        password1: '',
        password2: '',
        email: '',
        Intro:''
      }
    }
  },
  rules: {
    phonenumber: [//11位
      {required: true, message: '请输入手机号', trigger: 'blur'},
      {min: 11, max: 11, message: '手机号必须是11位', trigger:'blur'}
    ],
    username: [//可能重复
      {required: true, message: '请输入用户名', trigger: 'blur'}
    ],
    password1: [//4位
      {required: true, message: '请输入密码', trigger: 'blur'},
      {min: 4, max: 18, message: '密码在4~18位之间', trigger:'blur'}
    ],
    password2: [//与上个相同
      {required: true, message: '请确认密码', trigger: 'blur'},
    ],
    email: [//格式
      {required: false, message: '请输入邮箱', trigger: 'blur'}
    ]
  },
  methods: {
    doRegister () {
      this.$refs.infoForm.validate((valid) => {
        if (valid && this.user.password1 === this.user.password2) {
          postNewUser(this.user.username, this.user.password1, this.user.password2, this.user.phonenumber, this.user.email)
            .then(response => {
              console.log(response)
              this.$message({
                message: '注册成功！',
                type: 'success'
              })
              this.$router.push({ path: '/Login' })
            }).catch(error => {
              console.log(error)
              if(error.code === 400){
                this.$message({
                  message: '用户名已存在！',
                  type: 'error'
                })
              }
              else{
                this.$message({
                  message: '注册信息有误，请重新填写！',
                  type: 'error'
                })
              }
            })
        }
      })
    }
  }
}
</script>



