<template>
  <div v-title data-title="个人中心-修改信息">
    <UserNav></UserNav>
    <el-col span="20"><!-- v-if="rightUser"-->
      <el-main style="background-color: white; margin-right: 200px;">
        <el-form ref="infoForm" :model="user" :rules="rules" >
          <el-form-item prop="head">
            <el-row>点击图片即可修改头像<br>
             <el-upload
                class="avatar-uploader"
                action="false"
                ref="upload"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :auto-upload="false"
                :http-request="upload_head"
                :before-upload="beforeAvatarUpload">
                <img v-if="user.head" :src="user.head" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-row>
          </el-form-item>
          <el-form-item prop="UserName" style="margin-top: 15px;">
            <el-row>修改用户名:
              <el-input v-model="user.UserName" placeholder="请输入用户名" style="width: 220px;"></el-input>
            </el-row>
          </el-form-item>
          <br>
          <el-form-item prop="password1">
            <el-row>修改密码：
              <el-input type="password" v-model="user.password1" placeholder="请输入密码" style="width: 220px;">
              </el-input>
            </el-row>
          </el-form-item>
          <br>
          <el-form-item prop="password2">
            <el-row>确认密码：
              <el-input type="password" v-model="user.password2" placeholder="确认密码" style="width: 220px;">
              </el-input>
            </el-row>
          </el-form-item>
          <br>
          <el-form-item prop="email">
            <el-row>修改邮箱：
              <el-input v-model="user.email" placeholder="请输入邮箱" style="width: 220px;"></el-input>
            </el-row>
          </el-form-item>
          <br>
          <el-form-item prop="Intro">
            <el-row>
              修改个性签名：
              <el-input
                type="textarea"
                style="width: 400px;"
                placeholder="请输入15字以内个性签名"
                :autosize="{ minRows: 2, maxRows: 4}"
                v-model="user.Intro">
              </el-input>
            </el-row>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">确认修改</el-button>
          </el-form-item>
        </el-form>
      </el-main>
    </el-col>
  </div>
</template>

<script>
import UserNav from './UserCenter'
import {changeUserInfo, getUserInfo, getUserInfo2} from '../../api/api'
import axiosInstance from "../../api";
export default {
  name: 'ChangeInfo',
  data () {
    return {
      uploadurl: 'http://127.0.0.1:8000/users/',
      rightUser: true,
      user: {
        head: 'https://cube.elemecdn.com/9/c2/f0ee8a3c7c9638a54940382568c9dpng.png',
        UserName: '',
        fansNum: 13,
        followsNum: 70,
        Intro: '',
        registerDate: '2018-10-23',
        email: '',
        password1: '',
        password2: '',
        pswd: true,
        id:0
      },
      rules: {
        UserName: [
          {required: false, message: '在15个字符以内', trigger: 'blur'}
        ]
      },
      formData: null,
      headFile:null,
    }
  },
  components: {
    UserNav
  },
  watch:{
  },
  methods: {
    onSubmit () {
      // this.$refs.infoForm.validate，这是表单验证
      this.$refs.upload.submit()// 提交图片
      if(this.formData === null){
        this.formData = new FormData()
      }
      this.formData.append('username', this.user.UserName)
      this.formData.append('password', this.user.password1)
      this.formData.append('email', this.user.email)
      this.formData.append('introduction', this.user.Intro)
      console.log(this.user.head)
      let axios = axiosInstance
      this.$refs.infoForm.validate((valid) => {
        if (valid && this.password1 === this.password2) {
          axios.put('http://127.0.0.1:8000/users/' + this.user.id + '/', this.formData).then(response => {
            if (response.status === 200) {
              console.log(response)
              this.$message({
                message:'修改信息成功',
                type:'success'
              })
            this.user.UserName = response.data.username
            this.user.Intro = response.data.introduction
            this.user.registerDate = response.data.reg_date
            this.user.email = response.data.email
            this.user.id = response.data.id
            this.user.head = response.data.head
            } else {
              console.log(response)
              this.$message({
                message:'修改信息失败',
                type:'error'
              })
            }
          })
        } else {
          this.$message({
            message:'信息填写错误',
            type:'error'
          })
        }
      })
    },
    upload_head(){
      console.log('uploading the head')
      this.formData = new FormData()
      console.log(this.headFile)
      this.formData.append('head', this.headFile)
    },
    handleAvatarSuccess (res, file) {
      this.user.head = URL.createObjectURL(file.raw)
      console.log(this.user.head)
    },
    beforeAvatarUpload (file) {
      console.log('Before uploading')
      const isJPG = /image/.test(file.type)
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message({
          message:'上传文件只能是图片格式！',
          type:'error'
        })
      }
      if (!isLt2M) {
        this.$message({
          message:'上传头像图片大小不能超过 2MB!',
          type:'error'
        })
      }
      if(isJPG && isLt2M) {
        this.headFile = file
        console.log(this.headFile)
      }
      console.log(isJPG && isLt2M)
      return isJPG && isLt2M
    },
    getInfo () {
      getUserInfo().then(response => {
        if (response.status === 200) {
          console.log(response)
          this.user.UserName = response.data.username
          this.user.Intro = response.data.introduction
          this.user.registerDate = response.data.reg_date
          this.user.email = response.data.email
          this.user.id = response.data.id
          this.user.head = response.data.head
        } else {
          this.$message({
            message: '获取信息失败' + response.message,
            type: 'error'
          })
        }
      }).catch(error => {
        console.log(error)
        alert('获取信息失败')
      })
    },
    getUrl(){
      console.log(this.uploadurl+this.user.id+'/')
      return this.uploadurl + this.user.id + '/'
    },
  },
  mounted () {
    this.getInfo()
    if (this.user.UserName === this.GLOBAL.username) {
      this.rightUser = true
    } else this.rightUser = false
  }
}
</script>

<style scoped>
    .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .el-message-box{
    z-index: 2000;
  }
</style>



